import { Pie } from './Pie';

const pieInstance = new Pie(
  document.getElementById('container') as HTMLElement,
  {
    //
    colors: [
      ['#4291af', '#327184'],
      ['#f5c067', '#a16c3d'],
      ['#69ca87', '#3f874f'],
      ['#58a6e3', '#244f85'],
    ],
    //
    data: [
      { name: '', value: 4 },
      { name: '', value: 3 },
      { name: '', value: 2 },
      { name: '2', value: 1 },
    ],
    planeImage: '/plane.png',
  }
);

window.addEventListener('resize', () => {
  pieInstance.onResize();
});
