import * as THREE from 'three';
import Stats from 'three/examples/jsm/libs/stats.module.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
// 
declare global {
  interface Window {
    ThreeBase: ThreeBase;
  }
}

export class ThreeBase {
  container: HTMLElement | null = null;
  renderer: THREE.WebGLRenderer | null = null;
  scene: THREE.Scene | null = null;
  camera: THREE.PerspectiveCamera | null = null;
  initCameraPos = [80, 100, 0];
  stats: Stats | null = null;
  private isAxis = false;
  private isStats = false;
  private isRaycaster = false;
  controls: OrbitControls | null = null;
  threeAnimation: number | null = null;
  constructor(options: {
    isAxis: boolean;
    isStats: boolean;
    isRaycaster: boolean;
  }) {
    this.isAxis = options.isAxis;
    this.isStats = options.isStats;
    this.isRaycaster = options.isRaycaster;
  }

  // 3d
  initThree(el: HTMLElement) {
    window.ThreeBase = this;
    this.container = el;
    // 
    THREE.Cache.enabled = true;
    // 
    this.renderer = new THREE.WebGLRenderer({
      // 
      antialias: true,
      // 
      alpha: true,
      // 
      logarithmicDepthBuffer: true,
    });
    // 
    this.renderer.setClearColor(0x000000, 0);
    // 
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(
      this.container.offsetWidth,
      this.container.offsetHeight
    );
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    // dom
    this.container.appendChild(this.renderer.domElement);
    // 
    this.scene = new THREE.Scene();
    // 
    this.camera = new THREE.PerspectiveCamera(
      40,
      this.container.offsetWidth / this.container.offsetHeight,
      1,
      1000
    );
    this.camera.position.set(
      ...(this.initCameraPos as [number, number, number])
    );

    if (this.isAxis) {
      const axesHelper = new THREE.AxesHelper(500);
      this.scene.add(axesHelper);
    }

    if (this.isStats) {
      this.stats = new Stats();
      this.stats.dom.style.position = 'absolute';
      this.stats.dom.style.top = '0px';
      this.container.appendChild(this.stats.dom);
    }

    if (this.isRaycaster) {
      this.initRaycaster();
    }

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);

    const animate = () => {
      this.threeAnimation = requestAnimationFrame(animate);
      this.renderer.render(this.scene, this.camera);
      if (this.isStats) {
        this.stats.update();
      }
      this.animateAction();
    };

    animate();
  }
  // 
  initRaycaster() {}

  // 
  animateAction() {}

  // 
  cleanAll() {
    cancelAnimationFrame(this.threeAnimation);
    this.controls && this.controls.dispose();
  }

  onResize() {
    if (this.container) {
      this.camera.aspect =
        this.container.offsetWidth / this.container.offsetHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(
        this.container.offsetWidth,
        this.container.offsetHeight
      );
    }
  }
}
