import { ThreeBase } from './ThreeBase';
import * as THREE from 'three';
import {
  createBasicMaterial,
  createGradientColors,
  createLinearGradientMaterial,
  getDrawColors,
  isArrayWithHexColors,
  isColorArray,
} from './utils';

// 
let textureLoader = new THREE.TextureLoader();

interface ChatOptionsType {
  angel: number;
  offset: number;
  radiusDiff: number;
  startRadius: number;
  axis: THREE.Vector3;
  height: number;
  outerRadius: number;
  innerRadius: number;
  name?: string | number;
}
export interface ChatOptions {
  /**
   *  ['#fff']  [['#fff','#000']]
   */
  colors: string[] | [string, string][];
  /**
   * 
   */
  data: { name: string; value: number }[];
  /**
   * 
   */
  maxHeight?: number;
  /**
   * 
   */
  baseHeight?: number;
  /**
   * 
   */
  outerRadius?: number;
  /**
   * 
   */
  innerRadius?: number;
  /**
   * 
   */
  isAnimate?: boolean;
  /**
   * 
   */
  planeImage?: string;
}
export class Pie extends ThreeBase {
  private group: THREE.Group | null = null;
  constructor(element: HTMLElement, options: ChatOptions) {
    super({ isAxis: true, isStats: false, isRaycaster: false });
    this.initThree(element);
    this.createChat(options);
  }
  private createChat(_options: ChatOptions) {
    const options = {
      //
      maxHeight: 10,
      //
      baseHeight: 10,
      //
      outerRadius: 30,
      //
      innerRadius: 15,
      //
      isAnimate: false,
      ..._options,
    };
    // group
    if (this.group) {
      this.scene?.remove(this.group);
      this.group = null;
    }
    // 
    if (options.data.length == 0) {
      return;
    }
    // 
    const linearGradientColorList = [];
    // 
    let colors = [];
    //  simple gradient
    let currentColorType: 'simple' | 'gradient' = 'simple';
    // 
    if (isArrayWithHexColors(options.colors)) {
      // 
      colors = getDrawColors(options.colors, 10);
      currentColorType = 'simple';
    }

    if (isColorArray(options.colors)) {
      //
      colors = createGradientColors(options.colors, 20);
      currentColorType = 'gradient';
    }

    if (colors.length == 0) {
      throw new Error('');
    }
    let { baseHeight, maxHeight, outerRadius, innerRadius } = options;
    let sum = 0;
    let min = Number.MAX_SAFE_INTEGER;
    let max = 0;

    // 
    for (let i = 0; i < options.data.length; i++) {
      let item = options.data[i];
      sum += item.value;
      if (min > item.value) {
        min = item.value;
      }
      if (max < item.value) {
        max = item.value;
      }
    }
    //  -  
    let startRadius = 0;
    let valueDiff = max - min;
    let allHeight = maxHeight - baseHeight;

    let group = new THREE.Group();
    this.group = group;
    this.scene.add(group);
    const spacing = 5;
    // 
    for (let index = 0; index < options.data.length; index++) {
      let objGroup = new THREE.Group();
      objGroup.name = 'pie_group_' + index;
      const targetColor = options.colors[index % options.colors.length];
      let item = options.data[index];
      //
      let angel = (item.value / sum) * Math.PI * 2;
      // 
      const offset = (spacing / 1e3) * Math.PI * 2;
      //
      let height = baseHeight + ((item.value - min) / valueDiff) * allHeight;
      const chatOptions: ChatOptionsType = {
        angel,
        offset,
        radiusDiff: outerRadius - innerRadius,
        startRadius,
        axis: new THREE.Vector3(1, 0, 0),
        height,
        outerRadius,
        innerRadius,
      };

      //3D
      if (item.value) {
        const isSimpleColor = currentColorType === 'simple';
        const createMaterial = isSimpleColor
          ? createBasicMaterial
          : createLinearGradientMaterial;
        //
        const cs = colors[index % colors.length];
        const getColor = targetIndex => {
          if (isSimpleColor) {
            return cs[targetIndex];
          } else {
            return [cs[0][targetIndex], cs[1][targetIndex]];
          }
        };
        // 
        createOutRing(objGroup, createMaterial(getColor(1)), chatOptions);
        // 
        createInnerRing(objGroup, createMaterial(getColor(2)), chatOptions);
        // 
        createUpSide(objGroup, createMaterial(getColor(0)), chatOptions);
        // 
        createDownSide(objGroup, createMaterial(getColor(3)), chatOptions);
        // 
        createFontSlide(objGroup, createMaterial(getColor(4)), chatOptions);
        // 
        createEndSlide(objGroup, createMaterial(getColor(4)), chatOptions);

        if (options.planeImage) {
          // 
          createPlane(objGroup, {
            ...chatOptions,
            width: 90,
            height: 90,
            name: index,
            image: options.planeImage,
          });
        }
        group.add(objGroup);
      }
      // +
      startRadius = angel + startRadius;
    }
  }
}

const createOutRing = (
  group,
  material,
  { outerRadius, height, startRadius, offset, angel, name }: ChatOptionsType
) => {
  //
  let geometry = new THREE.CylinderGeometry(
    outerRadius,
    outerRadius,
    height,
    24,
    24,
    true,
    startRadius + offset,
    angel - offset * 2
  );
  let mesh = new THREE.Mesh(geometry, material);
  mesh.position.y = height * 0.5;
  mesh.name = 'pie_out_' + name;
  group.add(mesh);
};

//
const createInnerRing = (
  group,
  material,
  { innerRadius, height, startRadius, offset, angel, name }: ChatOptionsType
) => {
  let geometry = new THREE.CylinderGeometry(
    innerRadius,
    innerRadius,
    height,
    24,
    24,
    true,
    startRadius + offset,
    angel - offset * 2
  );
  let mesh = new THREE.Mesh(geometry, material);
  mesh.position.y = height * 0.5;
  mesh.name = 'pie_inner_' + name;
  group.add(mesh);
};

// 
const createUpSide = (
  group,
  material,
  {
    innerRadius,
    outerRadius,
    startRadius,
    offset,
    angel,
    height,
    name,
  }: ChatOptionsType
) => {
  let geometry = new THREE.RingGeometry(
    innerRadius,
    outerRadius,
    32,
    1,
    startRadius + offset,
    angel - offset * 2
  );

  //
  let mesh = new THREE.Mesh(geometry, material);
  mesh.name = 'pie_up_' + name;
  mesh.rotateX(-0.5 * Math.PI);
  mesh.rotateZ(-0.5 * Math.PI);
  mesh.position.y = height;
  group.add(mesh);
};
// 
const createDownSide = (
  group,
  material,
  {
    innerRadius,
    outerRadius,
    startRadius,
    offset,
    angel,
    height,
    name,
  }: ChatOptionsType
) => {
  let geometry = new THREE.RingGeometry(
    innerRadius,
    outerRadius,
    32,
    1,
    startRadius + offset,
    angel - offset * 2
  );

  let mesh = new THREE.Mesh(geometry, material);
  mesh.name = 'pie_down_' + name;
  mesh.rotateX(-0.5 * Math.PI);
  mesh.rotateZ(-0.5 * Math.PI);
  mesh.position.y = 0;
  group.add(mesh);
};

const createFontSlide = (
  group,
  material,
  {
    innerRadius,
    startRadius,
    height,
    offset,
    radiusDiff,
    name,
    axis,
  }: ChatOptionsType
) => {
  //1
  const g = new THREE.PlaneGeometry(radiusDiff, height);
  const plane = new THREE.Mesh(g, material);
  plane.position.y = height * 0.5;
  plane.position.x = 0;
  plane.position.z = 0;
  plane.name = 'pie_front_' + name;
  plane.rotation.y = startRadius + offset + Math.PI * 0.5;
  plane.translateOnAxis(axis, -(innerRadius + 0.5 * radiusDiff));
  group.add(plane);
};

const createEndSlide = (
  group,
  material,
  {
    innerRadius,
    startRadius,
    height,
    offset,
    radiusDiff,
    name,
    axis,
  }: ChatOptionsType
) => {
  const g = new THREE.PlaneGeometry(radiusDiff, height);
  const plane = new THREE.Mesh(g, material);
  plane.position.y = height * 0.5;
  plane.position.x = 0;
  plane.position.z = 0;
  plane.name = 'pie_end_' + name;
  plane.rotation.y = startRadius + offset + Math.PI * 0.5;
  plane.translateOnAxis(axis, -(innerRadius + 0.5 * radiusDiff));
  group.add(plane);
};

const createPlane = (
  group,
  _options: ChatOptionsType & {
    width: number;
    image: ChatOptions['planeImage'];
  }
) => {
  const options = {
    width: 100,
    height: 100,
    ..._options,
  };
  // 
  textureLoader.load(options.image, function (texture) {
    // 
    let imageMaterial = new THREE.MeshBasicMaterial({
      map: texture,
      side: THREE.DoubleSide,
      transparent: true,
    });
    imageMaterial.blending = THREE.NormalBlending;

    // 
    let imageGeometry = new THREE.PlaneGeometry(options.width, options.height);
    let imageMesh = new THREE.Mesh(imageGeometry, imageMaterial);
    imageMesh.position.set(0, 0, 0); // 
    imageMesh.rotation.x = -Math.PI / 2; // 
    group.add(imageMesh);
  });
};
